.site-header .site-title {
  font-size: $h1;
  color: $dark-gray;
}

.site-header a {
  font-size: $h3;
  font-weight: 300;
  text-decoration: none;

  &:hover,
  &:focus,
  &:active {
  	text-decoration: none;
  }
}

.site-header nav a:hover,
.site-header nav a:focus,
.site-header nav a:active,
.site-header .nav-active {
  border-bottom: 2px solid $link-color;
  color: $link-hover-color;
}

.site-nav a + a {
  margin-left: $space-2;
}